body {
  font-family: lato, sans-serif;
}

img {
  height: 400px;
  width: 500px;
  margin-top: 20px;
}

select {
  border: 1px solid gray;
  border-radius: 5px;
  padding: 5px;
  font-size: 24px;
  font-weight: 700;
}

.loader, .loader:before, .loader:after {
  width: 1em;
  height: 4em;
  background: #047ac3;
  animation: load1 1s ease-in-out infinite;
}

.loader {
  color: #047ac3;
  text-indent: -9999em;
  margin: 88px auto;
  font-size: 11px;
  animation-delay: -.16s;
  position: relative;
  transform: translateZ(0);
}

.loader:before, .loader:after {
  content: "";
  position: absolute;
  top: 0;
}

.loader:before {
  animation-delay: -.32s;
  left: -1.5em;
}

.loader:after {
  left: 1.5em;
}

@keyframes load1 {
  0%, 80%, 100% {
    height: 4em;
    box-shadow: 0 0;
  }

  40% {
    height: 5em;
    box-shadow: 0 -2em;
  }
}

.loader {
  width: 1em;
  height: 1em;
  text-indent: -9999em;
  border-radius: 50%;
  margin: 100px auto;
  font-size: 25px;
  animation: load5 1.1s infinite;
  position: relative;
  transform: translateZ(0);
